<template>
    <div>
        <van-card
        v-for="(item,index) in detail"
        :key="index"
        :num="item.num"
        :price="item.price"
        :title="item.cont"
        :thumb="item.url"
        >
            <div slot="footer">
                <van-button size="mini"></van-button>
                <van-button size="mini" @click="del(index)"></van-button>
            </div>
        </van-card>
        <router-link to="/Shop" class="link">
            <h3></h3>
        </router-link>
        <div class="bottom12"></div>
    </div>
</template>

<script>
export default {
    name:'Shopping',
    data(){
        return {
            detail:""
        }
    },
    created(){
        this.detail = JSON.parse(localStorage.getItem("sp"))
        // console.log("this.detail",this.detail)
        
    },
    methods:{
        del(i){
            this.detail.splice(i,1)
            localStorage.setItem("sp",JSON.stringify(this.detail))
            console.log(this.detail)
            
            let num=0
            for(let j = 0;j<this.detail.length;j++){
                num+=this.detail[j].num
                if(this.detail.lenght==0){
                    num=0
                }
                this.$bus.$emit('getPickNum',num);
                console.log(this.detail[j].num)
            }
            
        }
    }
}
</script>
<style scoped>
    .bottom12{
    float: left;
    width: 100%;
    height: 5.2rem
    }
    .link{
        font-size: 1.5rem;
        line-height: 2.8rem;
        font-family: 'Courier New', Courier, monospace;
        text-align: center;
        margin-top: 2rem;
        color: darkslateblue
    }

</style>
