<template>
    <div class="login">
        <h3></h3>
        <van-cell-group class="dl">
            <van-field
                v-model="username"
                required
                clearable
                label=""
                right-icon="question-o"
                placeholder=""
                @click-right-icon="$toast('question')"
            />

            <van-field
                v-model="password"
                type="password"
                label=""
                placeholder=""
                required
            />
        </van-cell-group>
        <van-button type="primary" size="large" class="btn1" @click="login"></van-button>
        <router-link to="/reg">
            <van-button plain type="danger" class="btn2" size="large"></van-button>
        </router-link>
        <div class="bottom12"></div>
    </div>
</template>

<script>
export default {
    name:'Login',
    data(){
        return {
            username:"",
            password:""
        }
    },
    methods:{
        login(){
        this.$axios({
            url:'/api/login',
            method:'post',
            data:{username:this.username,password:this.password,save:true}
        }).then(
            res=>{
                if(res.data.err===0){
                    console.log(res.data);
                    this.$router.push('/home');
                    var user = { id : Date.now() , username : this.username };
                    console.log(user)
                    localStorage.setItem('user',JSON.stringify(user));
                }else{
                    console.log(res.data)
                    this.mess = res.data.mess
                }
            })
        }
    }
}
</script>
<style scoped>
    h3{
        font: 2.4rem/3rem "";
        text-align: center
    }
    .login{
        margin-top:8rem
    }
    .dl{
        margin-top:2rem
    }
    .btn1{
        margin-top:2rem
    }
    .btn2{
        margin-top:1rem
    }
    .bottom12{
    float: left;
    width: 100%;
    height: 5.2rem
    }
</style>