<template>
  <div id="app">
    <!--  -->
    <van-nav-bar title="Aimlessly" :fixed="fixed" :border="fixed" class="header"/>
    <!--  -->
    <transition name="fade" mode="out-in">
      <router-view/>
    </transition>
    <!--  -->
    <div class="tabBar" v-model="selected">
      <ul>
        <li v-for='(tab,index) in tabs' :key="tab.id" id="tab.title" :id="tab.title">
          <router-link :to="tab.routerName" @click.native="changeHash(index)" :class='{"link-active":index==currentIndex}'>
            <img :src="tab.imgSrc" alt="">
            <van-tag round type="danger" v-if="index===2 && pickNum!=0">{{pickNum}}</van-tag>
            <p>{{ tab.title }}</p>
          </router-link>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
import home from './assets/img/home.png';
import vip from './assets/img/vip.png';
import shopping from './assets/img/shopping.png';
import search from './assets/img/search.png';
let tabs=[
  {id:1,title:"",imgSrc:home,routerName:{name:'home'}},
  {id:2,title:"",imgSrc:vip,routerName:{name:'vip'}},
  {id:3,title:"",imgSrc:shopping,routerName:{name:'shopping'}},
  {id:4,title:"",imgSrc:search,routerName:{name:'search'}}
]
export default {
  name:'app',
  data(){
    return {
      selected:'',
      fixed:true,
      tabs,
      currentIndex:0,
      pickNum:0
    }
  },
  methods:{
    changeHash(index){
      this.currentIndex = index;
    }
  },
  // beforeCreate(){
  //     if(sp){
  //     let Num1=0
  //     for(let i=0;i<sp.length;i++){
  //       Num1 += sp[i].num
  //       // console.log(Num1)
  //       this.pickNum = Num1
  //     }
  //   }
  // },
  created(){
    //
    let sp = JSON.parse(localStorage.getItem('sp'));
      if(sp){
      let Num1=0
      for(let i=0;i<sp.length;i++){
        Num1 += sp[i].num
        // console.log(Num1)
        this.pickNum = Num1
      }
    }    
    this.$bus.$on('sendPickNum',(data)=>{
      // console.log(data[0])
      this.pickNum += data[0];
      if(sp){
        for(let j = 0;j<sp.length;j++){
          if(sp[j].cont == data[1]){         
            sp[j].num +=data[0];
            this.pickNum=sp[j].num
            localStorage.setItem("sp",JSON.stringify(sp))
          }
        }
      };
    })
  },
  updated(){
    this.$bus.$on('getPickNum',(data)=>{
      console.log(data)
      this.pickNum=data
    })
  }
}
</script>
