<template>
    <div class="shopdetail">
        <Navbar title=""></Navbar>
        <div class="outer-swiper">
            <img :src="detail.url">
        </div>
        <div class="product-desc">
            <ul>
                <li>
                    <span class="product-desc-span">
                        {{detail.cont}}
                    </span>
                </li>
                <li class="price-li">
                    {{detail.price}}
                </li>
                <li class="number-li">
                    
                    <span @click="substract">-</span>
                    <span :v-model="pickNum">{{pickNum}}</span>
                    <span @click="add">+</span>
                </li>
                <li class="btnAll">
                    <van-button round type="danger" class="left"></van-button>
                    <van-button round type="warning" @click="ballHander" class="right">
                        
                    </van-button>
                </li>
            </ul>
        </div>
        <transition name="ball" @after-enter="afterEnter">
            <div class="ball" v-if="isExist"></div>
        </transition>
        <div class="bottom12"></div>
    </div>
</template>
<script>
export default {
    name:"shopdetail",
    data(){
      return {
        detail:"",
        pickNum:1,
        isExist:false
      }
    },
    beforeCreate(){
    let id = this.$route.query.id

    this.$axios({
    url:`/api/follow/${id}`,
    }).then(
    res=>{
        this.detail=res.data.data

    })
    .catch(err=>{
        console.log(err)
    })
    },
    methods:{
        afterEnter(){
            this.isExist=false;
            //BUS
            this.$bus.$emit('sendPickNum',[this.pickNum,this.detail.cont])
        },
        ballHander(){
            this.isExist=true
            this.goods = localStorage.getItem("sp");

            if(this.goods){
                this.goods = JSON.parse(this.goods);
                var onoff=true;
                for(var i = 0;i<this.goods.length;i++){
                    if(this.goods[i].cont == this.detail.cont){
                        this.goods[i].num ++;
                        onoff = false;
                    }
                }
                    if(onoff){
                        this.goods.push({
                            cont:this.detail.cont,
                            price:this.detail.price,
                            url:this.detail.url,
                            num:this.pickNum
                        })
                    }
                localStorage.setItem("sp",JSON.stringify(this.goods))
            }else{

                this.goods=[{
                    cont:this.detail.cont,
                    price:this.detail.price,
                    url:this.detail.url,
                    num:this.pickNum
                }]
                console.log(this.pickNum)
                localStorage.setItem("sp",JSON.stringify(this.goods))
            }
        },
        substract(){
            if(this.pickNum==1){
                return;
            }
            this.pickNum--
        },
        add(){
            if(this.pickNum>=20){
                return;
            }
            this.pickNum++
        }
    }   
}
</script>
<style>
    .outer-swiper{width:90%;height:90%;border:.05rem solid #999;;margin:2rem auto;border-radius: 2rem;overflow: hidden;}
    .outer-swiper img{width:100%;height:100%;margin:0 auto}
    .product-desc ul li .product-desc-span{font:1rem/1.5rem "";padding:0 2rem;display:block;text-align: center}
    .price-li{font:1rem/1.5rem "";color:red;text-align: center}
    .number-li{font:1rem/1.5rem "";text-align: center}
    .number-li span{width:2rem;height: 2rem;display:inline-block;border:.05rem solid #999;margin-left: 2%;text-align: center;line-height: 2rem}
    .number-li span:nth-child(1){border-radius: 50%;background:cornflowerblue;color:cornsilk}
    .number-li span:nth-child(3){border-radius: 50%;background:cornflowerblue;color:cornsilk;margin-top:5%}
    .btnAll{display:flex;justify-content:space-around;margin-top:5%}
    

    .ball{
        width:1rem;
        height:1rem;
        background-color: red;
        border-radius: 50%;
        position: absolute;
        left:80% ;
        top:75%
    } 
    .ball-enter-active{
        animation:bounce-in 1s
    }
    .ball-leave{
        opacity: 0;
    }
    @keyframes bounce-in{
        0%{
            transform:translate3d(0,0,0);
        }
        50%{
            transform:translate3d(-10%,-50%,0);
        }
        75%{
            transform:tra nslate3d(-80%,0,0);
        }
        100%{
            transform:translate3d(-600%,1000%,0);
        }
        
    }
    .bottom12{
    float: left;
    width: 100%;
    height: 5.2rem
    }
</style>